import classes from "./SeasAndEps.module.css";

const SeasAndEps = ({
  seasAndEpsData,
  onChangeSeason,
  currentSsAndEps,
  onChangeEpisode,
}) => {
  const handleChangeSeason = (seasonNumber) => {
    onChangeSeason(seasonNumber);
  };
  const handleChangeEpisode = (episodeNumber) => {
    onChangeEpisode(episodeNumber);
  };

  return (
    <div className={classes["seasAndEps-container"]}>
      <div className={classes["seasons-container"]}>
        <h3>Seasons:</h3>
        <div className={classes.seasons}>
          {seasAndEpsData.map((season) => (
            <span
              onClick={handleChangeSeason.bind(null, season.season_number)}
              key={season.id}
            >
              {season.name}
            </span>
          ))}
        </div>
      </div>
      <div className={classes["episodes-container"]}>
        <h3>Episodes:</h3>
        <div className={classes.episodes}>
          {seasAndEpsData?.at(currentSsAndEps - 1).episodes.map((episode) => (
            <span
              onClick={handleChangeEpisode.bind(null, episode.episode_number)}
              key={episode.id}
            >
              {episode.episode_number}
            </span>
          ))}
        </div>
      </div>
    </div>
  );
};

export default SeasAndEps;
