import { useEffect, useState } from "react";
import { useParams } from "react-router-dom";
import { apiKey, baseMovieUrl, baseTvUrl, baseUrl } from "../../App";
import classes from "./WatchMovie.module.css";
import DetailInfo from "./DetailInfo";
import SeasAndEps from "./SeasAndEps";

const WatchMovie = () => {
  const { movieId, media } = useParams();
  const [movieOrTv, setMovieOrTv] = useState();
  const [currentSsAndEps, setcurrentSsAndEps] = useState({
    tvId: null,
    seasonNumber: 1,
    currentEpisode: 1,
    seasAndEpsData: null,
  });

  useEffect(() => {
    const fetchMovie = async (url) => {
      const req = await fetch(url);
      const res = await req.json();
      setMovieOrTv(res);
      if (media === "tv") {
        const seasonsNumber = res.number_of_seasons;
        const promises = [];
        for (let i = 1; i <= seasonsNumber; i++) {
          const reqSeasons = await fetch(
            `${baseUrl}/${media}/${movieId}/season/${i}?api_key=${apiKey}`
          );
          const resSeasons = await reqSeasons.json();
          promises.push(resSeasons);
        }
        const fetchedPromises = await Promise.all(promises);
        setcurrentSsAndEps((state) => {
          return { ...state, tvId: res.id, seasAndEpsData: fetchedPromises };
        });
      }
    };
    // fetchMovie(`${baseUrl}/${media}/${movieId}?api_key=${apiKey}`);
    fetchMovie(`${baseUrl}/${media}/${movieId}?api_key=${apiKey}`);
  }, [media, movieId]);

  const handleChangeSeason = (seasonNumber) => {
    setcurrentSsAndEps((state) => {
      return { ...state, seasonNumber, currentEpisode: 1 };
    });
  };
  const handleChangeEpisode = (seasonEpisode) => {
    setcurrentSsAndEps((state) => {
      return { ...state, currentEpisode: seasonEpisode };
    });
  };

  return (
    <>
      <div className={classes.container}>
        <div className={classes["player-container"]}>
          <iframe
            className={classes.player}
            src={
              media === "movie"
                ? `${baseMovieUrl}${movieId}`
                : currentSsAndEps.tvId &&
                  `${baseTvUrl}${currentSsAndEps.tvId}&s=${currentSsAndEps.seasonNumber}&e=${currentSsAndEps.currentEpisode}`
            }
            title="Movie player"
            frameBorder="0"
            allowFullScreen={true}
          ></iframe>
        </div>
        {media === "tv" && currentSsAndEps.seasAndEpsData && (
          <SeasAndEps
            seasAndEpsData={currentSsAndEps.seasAndEpsData}
            onChangeSeason={handleChangeSeason}
            onChangeEpisode={handleChangeEpisode}
            currentSsAndEps={currentSsAndEps.seasonNumber}
          />
        )}
      </div>
      <div className={classes.detail}>
        {movieOrTv && (
          <DetailInfo
            title={movieOrTv.title}
            overview={movieOrTv.overview}
            runtime={movieOrTv.runtime}
            release_date={movieOrTv.release_date}
            genres={movieOrTv.genres}
            media={media}
            movieId={movieId}
          />
        )}
      </div>
    </>
  );
};

export default WatchMovie;
